@import url(https://unpkg.com/primer/build/build.css);
@import url(https://fonts.googleapis.com/css?family=Rubik:400,500,700,900);
body {
  font-family: rubik, sans-serif;
}
.github-component {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}

body {
  background-color: #0a192f;
}

.no-padding {
  padding-left: 0px;
  padding-right: 0px;
}

.col-md-1,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-5,
.col-md-6,
.col-md-7,
.col-md-8,
.col-md-9,
.col-md-10,
.col-md-11,
.col-md-12 {
  padding-left: 0px;
  padding-right: 0px;
}

.aboutme {
  padding-top: 100px;
}

h1 {
  padding-top: 40px;
  padding-left: 15px;
  color: #ccd6f6;
}

span {
  color: #ccd6f6;
  text-align: center;
}

.aboutme .numbered-heading {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  width: 100%;
  white-space: nowrap;
}

.aboutme .numbered-heading::after {
  content: "";
  display: block;
  position: relative;
  top: 2px;
  width: 100%;
  height: 1px;
  margin-left: 20px;
  background-color: #d100ff;
}

.aboutme ul {
  display: grid;
  grid-template-columns: repeat(3, minmax(140px, 200px));
  padding: 0px;
  margin: 20px 0px 0px;
  overflow: hidden;
  list-style: none;
  text-align: left;
}

.aboutme li {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
}

.aboutme li::before {
  content: "▹";
  position: absolute;
  left: 0px;
  color: #d100ff;
}

.img-container {
  -webkit-align-self: center !important;
          align-self: center !important;
  text-align: center !important;
  position: relative;
  top: 45px;
}

.img-container img {
  width: 90%;
  border-radius: 20px;
  margin-top: 30px;
  max-width: 630px;
}

.presentacion {
  color: white;
  background: linear-gradient(rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5) 100%),
    url(/static/media/europa.36a99cd7.png);
  background-size: cover;
  background-position: center;

  background-repeat: no-repeat;
  width: 100%;

  text-align: center;

  padding-top: 90px;
  padding-bottom: 90px;
  max-width: -1px;
}

.presentacion span {
  font-size: 18px;
  font-family: "Calibre", "San Francisco", "SF Pro Text", -apple-system,
    system-ui, sans-serif;
}

.contact-list {
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 18px;
}

